import { Button } from "@/components/ui/button";
import Image from "next/image"

const page = async ({params}) => {
  const { id } = await params

  const getProducts = async ()=>{
    const response = await fetch(`https://fakestoreapi.com/products/${id}`);
    return response.json()
    
  }
  const products = await getProducts()

  return (
    <div className="flex justify-center mt-14 text-white">
        <div className="mr-4">
            <Image src={products.image} alt={products.title} width={700} height={700} className="w-80 h-80 rounded-md"/>
        </div>
        <div className="flex flex-col mt-3">
            <div className="w-80">
                <p className="mb-3">{products.title}</p>
                <p className="text-teal-400">{products.description}</p>
                <p className="mt-3">R$ {products.price}</p>
                <Button 
                onClick={()=>btnAddCart()}
                className="w-full max-w-sm min-w-[150px] text-white bg-teal-400 
                hover:bg-teal-500 cursor-pointer hover:-translate-y-1 duration-200 rounded-sm">
                Adicionar ao carrinho
                </Button>
            </div>
        </div>
    </div>
  )
}

export default page